#overlay {
  /*　画面全体を覆う設定　*/
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(255, 255, 255);

  /*　画面の中央に要素を表示させる設定　*/
  align-items: center;
  justify-content: center;
  overflow-y: auto; /* 縦方向にスクロール可能にする */
  overflow-x: hidden;

  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  /* その他のスタイル */
}
