.footer-menu-bar {
  text-align: center;
  position: fixed; /* 要素の位置を固定する */
  bottom: 0; /* 絶対位置を指定する(下0px) */
  background-color: rgba(255, 255, 255, 0.85); /* 背景は透過させる */
  z-index: 1000;
  width: 100%;
}
.footer {
  height: 50px; /* 例えば、50px程度のフッターの高さを設定する */
}

.icon_box {
  width: "200px";
  height: "100px";
  border: "1px solid black";
  width: 200px;
  height: 100px;
  border: 1px solid black;
}

.stars {
  position: relative;
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(0deg, #00dedc, #115d89, #080f1c);
}
